/* Set the background color or image for the entire app */
body {
  background-color: #fffff8; /* Light gray background */
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  font-size: 30px;
  color: black;
}

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 10px;
}

/* Style for the upload section */
input[type="file"] {
  margin: 20px;
  padding: 20px;
  background-color: #007bff;
  color: white;
  
  border: double;
  border-radius: 10px;
  cursor: pointer;
}

input[type="file"]:hover {
  background-color: #0056b3;
}

/* Image preview styling */
img {
  margin-top: 20px;
  width: 300px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Button styling */
button {
  background-color: #28a746;
  color: white;
  padding: 10px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
  margin-top: 10px;
}

button:hover {
  background-color: #118838;
}
